import React, { Component } from "react";
import InsuranceQuestion from "./InsuranceQuestion";
import axiosInstance from "../../../axiosApi";

/**
 * Component that contains elements and functions related to the Insurance Section of the questionnaire.
 *
 * Renders out a page meant to be routed through the questionnaire's navigation systems.
 *
 * @version 3.0.0
 */
class InsuranceSection extends Component {
  state = {
    insurancePolicyType: [
      { value: "LI", text: "Life Insurance" },
      { value: "DI", text: "Disability Insurance" },
      { value: "CI", text: "Critical Illness Insurance" },
    ],
    lifePolicyType: [
      { value: "WL", text: "Whole Life" },
      { value: "UL", text: "Universal Life" },
      { value: "TL", text: "Term Life" },
    ],
  };

  async componentDidMount() {
    await axiosInstance
      .get("/users/ins-get/" + this.props.email + "/")
      .then((result) => {
        this.props.update_client_info(result.data);
      })
      .catch((error) => {
        throw error;
      });
  }

  nextClicked = (e) => {
    this.props.update_data(this.props.step);
    this.props.next(e);
  };

  prevClicked = (e) => {
    this.props.update_data(this.props.step);
    this.props.previous(e);
  };

  /**
   * Handles when a child InsuranceQuestion component is added to the section.
   *
   * @param {data} data
   * @private
   */
  handle_on_change = (data) => {
    let clientData = this.props.clientData;
    for (let i = 0; i < clientData.length; ++i) {
      if (clientData[i].type === data.type) {
        clientData[i] = data;
      }
    }
    this.props.update_client_info(clientData);
  };

  /**
   * Handles when a child InsuranceQuestion component is removed from the section.
   *
   * @param {data} data
   * @private
   */
  remove_item = (data) => {
    let clientData = this.props.clientData.filter((type) => type.type !== data);
    this.props.update_client_info(clientData);
  };

  render() {
    return (
      <div className="d-flex flex-row flex-wrap p-5 justify-content-center">
        {/* Left Info Section */}
        <div
          className="d-flex flex-column p-4 mb-5 w-auto border rounded border-dark"
          style={{ maxWidth: 400 }}
        >
          <h1 className="display-6">Insurance</h1>
          <h3>Section 7</h3>
          <hr />
          <div className="container-fluid">
            <p class="text-wrap">
              This section contains questions regarding any Insurance you may
              have.
            </p>
            <p class="text-wrap">
              This <mark>data will be kept securely</mark> and will only be{" "}
              <mark>used to generate recommendations</mark> on what you should
              do to achieve your finanacial goals.
            </p>
            <p class="text-wrap">
              <mark>If you feel uncomfortable</mark> sharing certain
              information, please
              <mark>press on the 'Exit Questionnaire' button</mark> at the
              bottom right.
            </p>
          </div>
        </div>
        {/* Form Content */}
        <div className="d-flex flex-column p-4 ms-5 flex-grow-1 border rounded border-dark">
          {/* Questions go here */}
          {this.props.clientData.map((data, index) => (
            <InsuranceQuestion
              key={index}
              remove_item={this.remove_item}
              handle_on_change={this.handle_on_change}
              type={data.type}
              policy_type={data.policy_type}
              benefit={data.benefit}
              premium={data.premium}
            />
          ))}
          <div className="d-flex flex-row flex-wrap m-3 align-items-center">
            <h5 className="text-wrap me-5">Add a type of Insurance:</h5>
            <select
              className="form-control form-select-lg w-50"
              id="type"
              default=""
              onChange={(e) => {
                let clientData = [
                  ...this.props.clientData,
                  {
                    type: e.target.value,
                    policy_type: "",
                    benefit: 0,
                    premium: 0,
                  },
                ];

                this.props.update_client_info(clientData);
              }}
            >
              <option value="">Choose...</option>
              {this.state.insurancePolicyType.map(
                (type, index) =>
                  !this.props.clientData.some((e) => e.type === type.value) && (
                    <option key={index} value={type.value}>
                      {type.text}
                    </option>
                  )
              )}
            </select>
          </div>
          {/* Previous & Next Buttons */}
          <div className="d-flex flex-row w-100 mt-5 justify-content-between">
            <button
              type="button"
              className="btn btn-outline-primary"
              style={{ fontSize: 25 }}
              onClick={this.prevClicked}
            >
              Prev
            </button>
            <button
              type="button"
              className="btn btn-outline-primary"
              style={{ fontSize: 25 }}
              onClick={this.nextClicked}
            >
              Next
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default InsuranceSection;
