import React from "react";
import "./Adminpanel.css";
import "../../components/admin-globals/admin-navbar";
import AdminForm from "../questionnaire/FormSteps/AdminForm";
import "./AdminEdit.css";
import AdminNav from "../../components/admin-globals/admin-navbar";
import AdminViewUsers from "./AdminViewUsers";
import axiosInstance from "../../axiosApi";

class AdminEdit extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      clientData: {},
      userData: {
        is_active: null,
      },

      button_clicked: false,
      button_goback_clicked: false,
    };
  }

  handle_submit = (e) => {
    e.preventDefault();
    axiosInstance
      .post(
        "users/CEI-update/" + this.props.email + "/",
        this.state.clientData
      )
      .then((result) => result)
      .catch((error) => {
        throw error;
      });

    axiosInstance
      .post("users/user-update/" + this.props.email + "/", this.state.userData)
      .then((result) => result)
      .catch((error) => {
        throw error;
      });
  };

  async componentDidMount() {
    await axiosInstance
      .get("users/CEI-get/" + this.props.email + "/")
      .then((result) => {
        this.setState({ clientData: result.data });
      })
      .catch((error) => {
        throw error;
      });

    await axiosInstance
      .get("users/user-get/" + this.props.email + "/")
      .then((result) => {
        this.setState({ userData: result.data });
      })
      .catch((error) => {
        throw error;
      });
  }

  render() {
    if (this.state.button_clicked)
      return <AdminForm email={this.props.email} />;

    if (this.state.button_goback_clicked) return <AdminViewUsers />;

    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <AdminNav handle_logout={this.props.handle_logout} />
        </div>

        <div className="col d-flex flex-wrap text-wrap m-5 p-4 border rounded bg-light">
          <div className="container-fluid p-5 border rounded border-dark">
            {/* Label Row */}
            <div className="row">
              <div className="col-sm d-flex flex-column align-items-center">
                <h1 className="display-6">User Data</h1>
              </div>
            </div>

            <hr />
            {/* Content Row */}

            <div className="row">
              <div className="col-sm d-flex flex-column align-items-center">
                <div className="d-flex flex-row">
                  <h4 className="text-wrap mx-4">Active</h4>
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      checked={this.state.userData.is_active}
                      id="is_active"
                      onChange={() => {
                        let is_active = document.getElementById("is_active")
                          .checked;
                        let userData = {
                          ...this.state.userData,
                          is_active: is_active,
                        };
                        this.setState({ userData });
                      }}
                    />
                  </div>
                </div>
                <h4 className="text-wrap">First Name:</h4>
                <input
                  type="text"
                  className="form-control form-select-lg w-50"
                  id="first_name"
                  value={this.state.clientData.first_name}
                  onChange={() => {
                    let first_name = document.getElementById("first_name")
                      .value;
                    let clientData = {
                      ...this.state.clientData,
                      first_name: first_name,
                    };
                    this.setState({ clientData });
                  }}
                />
                <h4 className="text-wrap">Last Name:</h4>
                <input
                  type="text"
                  className="form-control form-select-lg w-50"
                  id="last_name"
                  value={this.state.clientData.last_name}
                  onChange={() => {
                    let last_name = document.getElementById("last_name").value;
                    let clientData = {
                      ...this.state.clientData,
                      last_name: last_name,
                    };
                    this.setState({ clientData });
                  }}
                />
                <h4 className="text-wrap">Email:</h4>
                <p> {this.props.email} </p>

                <h4 className="text-wrap">Company:</h4>
                <input
                  type="text"
                  className="form-control form-select-lg w-50"
                  id="company"
                  value={this.state.clientData.company}
                  onChange={() => {
                    let company = document.getElementById("company").value;
                    let clientData = {
                      ...this.state.clientData,
                      company: company,
                    };
                    this.setState({ clientData });
                  }}
                />
                <h4 className="text-wrap">Account Type:</h4>
                <select
                  className="form-control form-select-lg w-50"
                  id="account_type"
                  value={this.state.clientData.account_type}
                  onChange={() => {
                    let account_type = document.getElementById("account_type")
                      .value;
                    let clientData = {
                      ...this.state.clientData,
                      account_type: account_type,
                    };
                    this.setState({ clientData });
                  }}
                >
                  <option value={""}>Choose...</option>
                  <option value="ONE">Level 1</option>
                  <option value="TWO">Level 2</option>
                  <option value="THREE">Level 3</option>
                  <option value="FOUR">Level 4</option>
                  <option value="FIVE">Level 5</option>
                </select>
              </div>
              <div className="col-sm d-flex flex-column align-items-center">
                <button
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                  onClick={() => {
                    this.setState({
                      button_clicked: true,
                      email: this.props.email,
                    });
                  }}
                >
                  View and Update Questionnaire
                </button>

                <button
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                  onClick={this.handle_submit}
                >
                  Save Changes
                </button>

                <button
                  onClick={() => {
                    this.setState({
                      button_clicked: false,
                      button_goback_clicked: true,
                    });
                  }}
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                >
                  Go Back
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default AdminEdit;
