import React, { Component } from "react";
import "./Adminpanel.css";
import AdminNav from "../../components/admin-globals/admin-navbar";

class Adminpanel extends Component {
  state = {};
  render() {
    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <AdminNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column flex-wrap text-center m-5 border rounded bg-light">
            <h1> Welcome back Admin!</h1>
        </div>
      </div>
    );
  }
}

export default Adminpanel;
