import React, { Component } from "react";
import "./Questionnaire.css";

class QuestionnaireNavBar extends Component {
  navigate_away = (step) => {
    if (
      this.props.step === 1 ||
      this.props.step === 2 ||
      this.props.step === 6 ||
      this.props.step === 8
    ) {
      this.props.update_single_data(this.props.step);
    } else {
      this.props.update_multi_data(this.props.step);
    }
    this.props.setStep(step);
  };
  render() {
    return (
      <div className="d-flex flex-row justify-content-around">
        <button
          className="background"
          onClick={() => {
            this.navigate_away(1);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Personal
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.personal + "%",
                backgroundColor:
                  this.props.personal === 100
                    ? "green"
                    : this.props.personal < 50
                    ? "red"
                    : "orange",
              }}
            ></div>
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(2);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Mindset
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.mindset + "%",
                backgroundColor:
                  this.props.mindset === 100
                    ? "green"
                    : this.props.mindset < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(3);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Accounts
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.account + "%",
                backgroundColor:
                  this.props.account === 100
                    ? "green"
                    : this.props.account < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(4);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Assets
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.asset + "%",
                backgroundColor:
                  this.props.asset === 100
                    ? "green"
                    : this.props.asset < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(5);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Debt
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.debt + "%",
                backgroundColor:
                  this.props.debt === 100
                    ? "green"
                    : this.props.debt < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(6);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Income
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.income + "%",
                backgroundColor:
                  this.props.income === 100
                    ? "green"
                    : this.props.income < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(7);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Insurance
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.insurance + "%",
                backgroundColor:
                  this.props.insurance === 100
                    ? "green"
                    : this.props.insurance < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>

        <button
          className="background"
          onClick={(e) => {
            this.navigate_away(8);
          }}
        >
          <div styles={{ fontSize: 30, textAlign: "center", marginBottom: 20 }}>
            Misc
          </div>
          <div className="progressBackground">
            <div
              className="progressBar"
              style={{
                width: this.props.misc + "%",
                backgroundColor:
                  this.props.misc === 100
                    ? "green"
                    : this.props.misc < 50
                    ? "red"
                    : "orange",
              }}
            />
          </div>
        </button>
      </div>
    );
  }
}

export default QuestionnaireNavBar;
