import React from "react";
import "./profile.css";
import UserNav from "../../components/user-globals/user-navbar";

class ProfilePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      
      clientData:{
          first_name:"",
          last_name:"",
          email:this.props.email,
          company: "",
          preferred_name:"",
      },
      
    };
    

    
  }

  handle_submit = (e) => {
    e.preventDefault();
    console.log(this.state.clientData);
    console.log(this.props.email);
    
      var url = "http://127.0.0.1:8000/api/users/email-update/"+this.props.email+"/";
      fetch(url, {
        method: "POST",
        headers: {
          Authorization: "Basic " + btoa("401team:finovowelltothemoon"),
          "Content-type": "application/json",
        },
        body: JSON.stringify(this.state.clientData),
        
      })
      .then((response) => {
        return response.json();
      })
      .catch(function (error) {
        console.log("ERROR: ", error);
      });
  };


  componentDidMount() {
    var url =
      "http://127.0.0.1:8000/api/users/email-get/" + this.props.email + "/";
    fetch(url, {
      method: "get",
      headers: {
        Authorization: "Basic " + btoa("401team:finovowelltothemoon"),
        "Content-type": "application/json",
      },
    })
      
    .then((response) => {
        // When the post request returns succesfully, reset all the data.
        return response.json();
      })
      .then((json) => {
        let clientData = {
          company: json.company,
          first_name:json.first_name,
          last_name:json.last_name,
          preferred_name:json.preferred_name,
  };
        this.setState({ clientData });
        })
      .catch(function (error) {
        console.log("ERROR: ", error);
      });
  }
  
  render() {
    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <UserNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-wrap text-wrap m-5 p-4 border rounded bg-light">
          <div className="container-fluid p-5 border rounded border-dark">
            {/* Label Row */}
            <div className="row">
              <div className="col-sm d-flex flex-column align-items-center">
                <h1 className="display-6">Personal Information</h1>
              </div>
              <div className="col-sm d-flex flex-column align-items-center">
                <h1 className="display-6">Other</h1>
              </div>
            </div>
            <hr />
            {/* Content Row */}
            <div className="row">
              
              <div className="col-sm d-flex flex-column align-items-center">
                    <h4 className="text-wrap">First Name:</h4>
                    <input
                        type="text"
                        className="form-control form-select-lg w-50"
                        id="first_name"
                        value={this.state.clientData.first_name}
                        onChange={() => {
                            let first_name = document.getElementById("first_name").value;
                            let clientData = {
                            ...this.state.clientData,
                            first_name: first_name,
                            };
                            this.setState({ clientData });
                        }}
                        
                        />
                    <h4 className="text-wrap">Last Name:</h4>
                    <input
                        type="text"
                        className="form-control form-select-lg w-50"
                        id="last_name"
                        value={this.state.clientData.last_name}
                        onChange={() => {
                            let last_name = document.getElementById("last_name").value;
                            let clientData = {
                            ...this.state.clientData,
                            last_name: last_name,
                            };
                            this.setState({ clientData });
                        }}
                        
                        
                        />
                    <h4 className="text-wrap">Preferred Name:</h4>
                    <input
                        type="text"
                        className="form-control form-select-lg w-50"
                        id="preferred_name"
                        value={this.state.clientData.preferred_name}
                        onChange={() => {
                            let preferred_name = document.getElementById("preferred_name").value;
                            let clientData = {
                            ...this.state.clientData,
                            preferred_name: preferred_name,
                            };
                            this.setState({ clientData });
                        }}
                        
                        
                        />

                <h4 className="text-wrap">Email:</h4>
                <p> {this.props.email} </p>
                
                <h4 className="text-wrap">Company:</h4>
                <p> {this.state.clientData.company} </p>
              </div>
              <div className="col-sm d-flex flex-column align-items-center">
                <button
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                >
                  Update Questionnaire
                </button>
                <button
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                  onClick={this.handle_submit}
                >
                  Save Changes
                </button>
                <button
                  type="button"
                  className="btn btn-lg btn-primary w-100 my-3"
                >
                  Other?
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ProfilePage;
