import React from "react";
import AdminNav from "../../components/admin-globals/admin-navbar";
import axiosInstance from "../../axiosApi";
import { BsStar } from "react-icons/bs";

/**
 * Component that contains elements and functions related to mnnaging articles.
 * Actions such as setting featured, unfeaturing, and deleting can be done.
 *
 * Renders out a page meant to be routed through a navbar for admin side.
 * 
 * TODO: Searchbar functionality
 *
 * @version 2.0.0
 */
class ManageArticlesPage extends React.Component {
  // Instead of doing another API call, it's better to have 2 array in this.state.
  // 1 for storing all of the articles and 1 for filtered data.
  // Show the filtered data to the user but use the all_articles array to filter.
  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      activeItem: {
        id: null,
      },
    };
    this.handle_update = this.handle_update.bind(this);
    this.fetchArticles = this.fetchArticles.bind(this);
  }

  componentDidMount() {
    this.fetchArticles();
  }

  async fetchArticles() {
    await axiosInstance
      .get("blog/post-list/")
      .then((result) => {
        this.setState({
          articles: result.data,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  async handle_update(article) {
    let updateArticle = article;
    updateArticle.isFeatured = !article.isFeatured;

    await axiosInstance
      .post(`blog/post-update/${article.id}/`, updateArticle)
      .then((result) => {
        this.fetchArticles();
      })
      .catch((error) => {
        throw error;
      });
  }

  async handle_delete(article) {
    await axiosInstance
      .delete(`http://localhost:8000/api/blog/post-delete/${article.id}/`)
      .then((result) => {
        this.fetchArticles();
      })
      .catch((error) => {
        throw error;
      });
  }

  render() {
    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <AdminNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column m-5 p-3 border rounded bg-light">
          <div className="container-sm my-3 text-center align-self-center align-items-center">
            <h1 className="text-center">Manage Articles</h1>
            <hr />
          </div>
          {/* Content */}
          <div className="container-fluid align-self-center">
            <div className="row px-4">
              <div className="col-md-3 d-flex flex-column pe-5">
                {/* How */}
                <h4>Featuring Articles</h4>
                <hr />
                <p>
                  Locate your desired Article in the list of articles and press the 'Make Featured'
                  button. A little star will appear to the left of the post title. Press the same button
                  to unfeature an article.
                </p>
                {/* What */}
                <h4 className="mt-3">What Does Featuring an Article do?</h4>
                <hr />
                <p>
                  Featuring an article moves the article to the top of users article's page and keeps
                  it there for as long as it is featured. Featured articles are also shown on the user's
                  dashboard.
                </p>
                <h4 className="mt-3">Deleting Articles</h4>
                <hr />
                <p>
                  Press the 'Delete' button to delete an article. <mark>WARNING:</mark> There is no confirmation for deletion,
                  so only press it if you are sure you'd like to delete it.
                </p>
              </div>
              <div
                className="col-md d-flex flex-column p-3 border rounded border-dark"
                style={{ minHeight: 600 }}
              >
                {/* Download Section */}
                <p class="lead text-center mb-2">
                  Search articles by Title
                </p>
                <form className="d-flex flex-wrap justify-content-center m-3">
                  <div className="form-floating w-75">
                    <input
                      className="form-control needs-validation"
                      type="text"
                      id="searchInput"
                      placeholder="Article Title"
                      required
                    />
                    <label htmlFor="searchInput">Article Title</label>
                  </div>
                  <button
                    type="submit"
                    className="btn btn-lg btn-outline-primary ms-5 p-8"
                    id="searchButton"
                  >
                    Search
                  </button>
                </form>

                <div className="col-md d-flex flex-column border round border-dark">
                  <ul
                    className="list-group list-group-flush"
                    data-bs-spy="scroll"
                    data-bs-offset="0"
                  >
                    {/* Fetching & loading articles*/}
                    {this.state.articles.filter(article => article.isFeatured).map((article, index) => {
                      return (
                        <li className="list-group-item">
                          <div className="form-check"> 
                            <label className="d-flex form-check-label" for="flexCheckDefault">
                              <div>
                              <BsStar style={{color:"orange"}}/>
                              {" " + article.title}
                              </div>
                              <p className="ms-5 me-auto">{article.date_posted.split("T")[0]+" "}</p>
                              <button type="button" className="btn btn-warning btn-sm me-5" id={"update"+article.id}
                                      onClick={() => this.handle_update(article)}>Remove Feature</button>
                              <button type="button" className="btn btn-danger btn-sm" id={"delete"+article.id}
                                      onClick={() => this.handle_delete(article)}>Delete</button>
                            </label>
                          </div>
                        </li>
                      );
                    })}
                    {this.state.articles.filter(articles => !articles.isFeatured).map((articles, index) => {
                      return (
                        <li className="list-group-item">
                          <div className="form-check"> 
                            <label className="d-flex form-check-label" for="flexCheckDefault">
                              <p>{articles.title}</p>
                              <p className="ms-5 me-auto">{articles.date_posted.split("T")[0]+" "}</p>
                              <button type="button" className="btn btn-warning btn-sm me-5" id={"update"+articles.id} 
                                      onClick={() => this.handle_update(articles)}>Make Featured</button>
                              <button type="button" className="btn btn-danger btn-sm" id={"delete"+articles.id}
                                      onClick={() => this.handle_delete(articles)}>Delete</button>
                            </label>
                          </div>
                        </li>
                      );
                    })}
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ManageArticlesPage;
