import React from "react";
import "../../components/admin-globals/admin-navbar";
import AdminNav from "../../components/admin-globals/admin-navbar";
import axiosInstance from "../../axiosApi";

import "./AdminViewUsers.css";
import AdminEdit from "./AdminEdit";

class AdminViewUsers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      activeItem: {
        id: null,
      },
      user_email: "",
      button_clicked: false,
    };

    this.fetchTasks = this.fetchTasks.bind(this);
    this.getCookie = this.getCookie.bind(this);
  }

  getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  }

  componentDidMount() {
    this.fetchTasks();
  }

  async fetchTasks() {
    await axiosInstance
      .get("users/user-list/")
      .then((result) => {
        this.setState({
          users: result.data,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  handle_submit(e) {
    e.preventDefault();
    //Instead of doing another API call, it's better to have 2 array in this.state.
    //1 for storing all of the blogs and 1 for filtered data.
    //Show the filtered data to the user but use the all_blogs array to filter.
    //
    // e.preventDefault();
    // console.log("ITEM:", this.state.activeItem);
    // var csrftoken = this.getCookie("csrftoken");
    // var url = "http://localhost:8000/api/blog/post-list/";
    // fetch(url, {
    //   method: "GET",
    //   headers: {
    //     Authorization: "Basic " + btoa("401team:finovowelltothemoon"),
    //     "Content-type": "application/json",
    //     "X-CSRFToken": csrftoken,
    //   },
    // })
    //   .then((response) => {
    //     this.setState({
    //       activeItem: {
    //         id: null,
    //       },
    //     });
    //   })
    //   .catch(function (error) {
    //     console.log("ERROR: ", error);
    //   });
  }

  render() {
    if (this.state.button_clicked)
      return <AdminEdit email={this.state.user_email} />;

    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <AdminNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column flex-wrap m-5 p-5 bg-light">
          <h1 className="text-center">View / Edit User Information</h1>
          <hr />
          {/* Content */}
          <div className="container-fluid">
            {/* Search and Filter */}
            <div className="row">
              <div className="container-fluid">
                <form className="d-flex">
                  <button
                    className="btn btn-lg btn-outline-secondary dropdown-toggle w-25 me-5"
                    type="button"
                    data-bs-toggle="dropdown"
                    aria-expanded="false"
                  >
                    Filter
                  </button>
                  <ul className="dropdown-menu">
                    <li>
                      <a className="dropdown-item">Show All</a>
                    </li>
                    <hr className="dropdown-divider"></hr>
                    <li>
                      <a className="dropdown-item">Company</a>
                    </li>
                    <li>
                      <a className="dropdown-item">Never Logged In</a>
                    </li>
                    <li>
                      <a className="dropdown-item">Recently Active</a>
                    </li>
                    {/* Other filters */}
                  </ul>
                  <input
                    className="form-control me-2"
                    type="search"
                    placeholder="Search"
                    aria-label="Search"
                  />
                  <button
                    className="btn btn-lg btn-outline-success"
                    type="submit"
                  >
                    Search
                  </button>
                </form>
              </div>
            </div>
            <div
              className="row mt-3 overflow-auto border rounded border-dark"
              style={{ minHeight: 550 }}
            >
              {/* List of Users */}
              <div className="table-responsive">
                <table className="table table-striped table-hover text-center">
                  <thead>
                    <tr>
                      <th scope="col">#</th>
                      <th scope="col">First Name</th>
                      <th scope="col">Last Name</th>
                      <th scope="col">Email Address</th>
                      <th scope="col">Company</th>
                      <th scope="col">Status</th>
                      <th scope="col"></th>
                    </tr>
                  </thead>
                  <tbody>
                    {this.state.users.map((user, index) => {
                      return (
                        <tr key={index}>
                          <th scope="row">{index + 1}</th>
                          <td>{user.first_name}</td>
                          <td>{user.last_name}</td>
                          <td>{user.username}</td> {/* Note: username=email */}
                          <td>Company{/*user.clientemail.company*/}</td>
                          <td>None</td>
                          {/* Last online */}
                          {/* Other */}
                          <td>
                            <button
                              onClick={() => {
                                this.setState({
                                  button_clicked: true,
                                  user_email: user.username,
                                });
                              }}
                              type="button"
                              className="btn btn-lg btn-primary me-3 mb-3 align-self-end"
                              data-bs-toggle="modal"
                              data-bs-target="#questionnaireExitModal"
                            >
                              View and Edit User Details
                            </button>
                          </td>
                        </tr>
                      );
                    })}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default AdminViewUsers;
