import React from "react";
import "./App.scss";
import { BrowserRouter, Redirect, Route, Switch } from "react-router-dom";
import initialpage from "./pages/initialpages-app/initialpage";
import LoginApp from "./pages/login-app/loginapp";

import Adminpanel from "./pages/Admin-app/Adminpanel";
import RegisterNewCEIPage from "./pages/Admin-app/AdminRegisterNewCEIPage.js";
import DownloadReportsPage from "./pages/Admin-app/AdminDLReportsPage.js";
import ManageArticlesPage from "./pages/Admin-app/AdminManageArticlesPage.js";
import AdminNewArticlePage from "./pages/Admin-app/AdminNewArticlePage.js";
import AdminViewFilterUsers from "./pages/Admin-app/AdminViewFilterUsers.js";

import Dashboard from "./pages/User-app/dashboard.jsx";
import ArticlesPage from "./pages/User-app/articles.js";
import ArticlesPageReader from "./pages/User-app/ArticlesPageReader.js";
import ContactCoachPage from "./pages/User-app/contactCoachPage.js";
import FinancialToolsPage from "./pages/User-app/financialTools.js";
import HelpPage from "./pages/User-app/help.js";
import ProfilePage from "./pages/User-app/profile.js";

import Form from "./pages/questionnaire/FormSteps/Form";
import AdminEdit from "./pages/Admin-app/AdminEdit";
import AdminForm from "./pages/questionnaire/FormSteps/AdminForm";

import axiosInstance from "./axiosApi";

import { isEmptyObject } from "jquery";

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      logged_in: localStorage.getItem("access") ? true : false,
      displayed_form: "",
      user: {},
      invalidLogin: false,
      invalidSignup: false,
    };

    this.handle_login = this.handle_login.bind(this);
    this.handle_signup = this.handle_signup.bind(this);
    this.handle_logout = this.handle_logout.bind(this);
  }

  async handle_logout() {
    try {
      const response = await axiosInstance.post("users/logout/", {
        refresh: localStorage.getItem("refresh"),
      });
      localStorage.removeItem("access");
      localStorage.removeItem("refresh");
      axiosInstance.defaults.headers["Authorization"] = null;
      this.setState({
        logged_in: false,
        user: {},
        invalidLogin: false,
        invalidSignup: false,
      });
      return response;
    } catch (error) {
      throw error;
    }
  }

  async setUser(email) {
    await axiosInstance
      .get("/users/user-get/" + email + "/")
      .then((result) => {
        this.setState({
          user: result.data,
          invalidLogin: false,
          logged_in: true,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  async handle_login(e, data) {
    e.preventDefault();
    await axiosInstance
      .post("/token/", {
        username: data.email,
        password: data.password,
      })
      .then((result) => {
        axiosInstance.defaults.headers["Authorization"] =
          "JWT " + result.data.access;
        localStorage.setItem("access", result.data.access);
        localStorage.setItem("refresh", result.data.refresh);
        this.setUser(data.email);
      })
      .catch((error) => {
        this.setState({
          invalidLogin: true,
        });
        throw error;
      });
  }

  async handle_signup(e, data) {
    e.preventDefault();
    await axiosInstance
      .post("/users/signup/", {
        username: data.email,
        email: data.email,
        password: data.password,
      })
      .then((result) => {
        axiosInstance.defaults.headers["Authorization"] =
          "JWT " + result.data.token.access;
        localStorage.setItem("access", result.data.token.access);
        localStorage.setItem("refresh", result.data.token.refresh);
        this.setUser(data.email);
      })
      .catch((error) => {
        this.setState({
          invalidLogin: true,
        });
        throw error;
      });
  }

  async componentDidMount() {
    // This is used to make the user stay logged in even when the page is refreshed
    await axiosInstance
      .get("/users/current_user/")
      .then((result) => {
        this.setState({
          user: result.data,
          invalidLogin: false,
          logged_in: true,
        });
      })
      .catch((error) => {
        this.setState({
          logged_in: false,
        });
        throw error;
      });
  }

  render() {
    return (
      <BrowserRouter>
        <Switch>
          {/*General Paths*/}
          <Route path="/" exact component={initialpage} />

          <Route
            path="/login"
            exact
            render={() => (
              <LoginApp
                handle_login={this.handle_login}
                handle_signup={this.handle_signup}
                logged_in={this.state.logged_in}
                is_staff={this.state.user.is_staff}
                invalidLogin={this.state.invalidLogin}
                invalidSignup={this.state.invalidSignup}
              />
            )}
          />

          {/*User Paths*/}
          <Route
            path="/dashboard"
            exact
            render={() =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <Dashboard
                    logged_in={this.state.logged_in}
                    handle_logout={this.handle_logout}
                    user={this.state.user}
                  />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/dashboard/profile"
            exact
            render={() =>
              this.state.logged_in ? (
                <ProfilePage
                  handle_logout={this.handle_logout}
                  email={this.state.email}
                />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/dashboard/articles"
            exact
            render={() =>
              this.state.logged_in ? (
                <ArticlesPage handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/dashboard/articles/article/id/"
            render={() =>
              this.state.logged_in ? (
                <ArticlesPageReader handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/dashboard/contact-coach"
            exact
            render={() =>
              this.state.logged_in ? (
                <ContactCoachPage handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/dashboard/financial-tools"
            exact
            render={() =>
              this.state.logged_in ? (
                <FinancialToolsPage handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/dashboard/help"
            exact
            render={() =>
              this.state.logged_in ? (
                <HelpPage handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          {/*Admin Paths*/}
          <Route
            path="/admin"
            exact
            render={(props) =>
              this.state.logged_in ? (
                <Adminpanel handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/admin/emails"
            exact
            render={() =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <RegisterNewCEIPage
                    user={this.state.user}
                    handle_logout={this.handle_logout}
                  />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/admin/createpost"
            exact
            render={() =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <AdminNewArticlePage
                    user={this.state.user}
                    handle_logout={this.handle_logout}
                  />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/admin/feature"
            exact
            render={() =>
              this.state.logged_in ? (
                <ManageArticlesPage handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />
          <Route
            path="/admin/userinfo"
            exact
            render={() =>
              this.state.logged_in ? (
                <AdminViewFilterUsers handle_logout={this.handle_logout} />
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/admin/adminedit"
            exact
            render={() =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <AdminEdit
                    handle_logout={this.handle_logout}
                    user={this.state.user}
                  />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />

          {
            <Route
              path="/admin/reports"
              exact
              render={() =>
                this.state.logged_in ? (
                  <DownloadReportsPage handle_logout={this.handle_logout} />
                ) : (
                  <Redirect to="/" />
                )
              }
            />
          }

          {/*Questionnaire Paths*/}
          <Route
            path="/questionnaire"
            exact
            render={(props) =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <Form user={this.state.user} />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />

          <Route
            path="/controlquestionnaire"
            exact
            render={(props) =>
              this.state.logged_in ? (
                !isEmptyObject(this.state.user) && (
                  <AdminForm user={this.state.user} />
                )
              ) : (
                <Redirect to="/" />
              )
            }
          />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;
