import React from "react";
import "./dashboard.css";
import UserNav from "../../components/user-globals/user-navbar.js";
import { recommendations } from "./dash-content.js";
import { Link } from "react-router-dom";
import defaultImage from "../../images/article-image.jpg";
import axiosInstance from "../../axiosApi";

class Dashboard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      blogs: [],
      activeItem: {
        id: null,
      },
    };

    this.getCookie = this.getCookie.bind(this);
  }

  /**
   * Gets a cookie for the fetch operation.
   *
   * @param {string} name
   * @private
   */
  getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  }

  componentDidMount() {
    this.getBlogs();
  }

  async getBlogs() {
    await axiosInstance
      .get("/blog/post-list/")
      .then((result) => {
        this.setState({
          blogs: result.data,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  render() {
    if (!this.props.logged_in) {
    }
    return (
      <div className="d-flex flex-row overflow-auto min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <UserNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column justify-content-center align-items-center m-5 p-3 border rounded bg-light">
          <h1 id="user-dashboard-page-heading">
            {" "}
            Welcome back /First Name Last Name/!
          </h1>

          {/* Dashboard Content */}
          <div className="container-md mt-3 p-2 flex-grow-1 border rounded border-dark">
            {/* Notifications */}
            <div className="row">
              <div className="col-sm d-flex flex-column">
                <p className="text-center">
                  Notification Row that will only appear if user has
                  notifications, e.g. do the questionnaire
                  <a href="/questionnaire">ToQuestionnaire</a>
                </p>
                <hr />
              </div>
            </div>
            {/* Content */}
            <div className="row px-3">
              {/* Recommendations */}
              <div className="col-sm d-flex flex-column mt-3 align-items-center">
                <div className="display-6 mb-3">Recommendations</div>
                <div className="card w-100" id="dash-card">
                  <div className="accordion" id="accordionBod">
                    {recommendations.map((item, index) => {
                      return (
                        <div key={index} className={item.reccName}>
                          <div className="accordion-item">
                            <h2
                              className="accordion-header"
                              id={"heading" + item.order}
                            >
                              <button
                                className="accordion-button"
                                type="button"
                                data-bs-toggle="collapse"
                                data-bs-target={"#collapse" + item.order}
                                aria-expanded="true"
                                aria-controls={"collapse" + item.order}
                              >
                                {item.title}
                              </button>
                            </h2>
                            <div
                              id={"collapse" + item.order}
                              className="accordion-collapse collapse"
                              aria-labelledby={"heading" + item.order}
                              data-bs-parent="#accordionBod"
                            >
                              <div className="accordion-body">
                                {item.description}
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
                {/* Progress & Other */}
                <div className="card my-3">
                  <div className="bars">
                    <h1 id="progress-head">Progress</h1>
                    <div className="container d-flex justify-content-center padding">
                      <div className="row">
                        <div className="col-md-9 col-sm-6">
                          <div className="progress blue">
                            {" "}
                            <span className="progress-left">
                              {" "}
                              <span className="progress-bar"></span>{" "}
                            </span>{" "}
                            <span className="progress-right">
                              {" "}
                              <span className="progress-bar"></span>{" "}
                            </span>
                            <div className="progress-value">100%</div>
                          </div>
                        </div>
                        <h6 id="progress-foot">
                          This percentile is representative of your overall
                          completion of recommendations
                        </h6>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {/* Featured Articles */}

              <div className="col-sm d-flex flex-column mt-3 align-items-center">
                <div className="display-6 text-wrap">Featured Articles</div>
                <div className="d-flex flex-column">
                  {this.state.blogs
                    .filter((blog) => blog.isFeatured)
                    .map((blog, index) => {
                      console.log("blog", blog);
                      return (
                        <div
                          className="card mt-3"
                          id={"dash-card-id" + blog.id}
                        >
                          <img
                            src={defaultImage}
                            className="card-img-top"
                            alt="..."
                          ></img>
                          <div className="card-body">
                            <h5 className="card-title">{blog.title}</h5>
                            <p className="card-text">{blog.description}</p>
                            <Link
                              to={{
                                pathname:
                                  "/dashboard/articles/article/id/" + blog.id,
                                state: blog,
                              }}
                              type="button"
                              className="btn btn-primary"
                            >
                              Expand Article
                            </Link>
                          </div>
                        </div>
                      );
                    })}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Dashboard;
