import React from "react";
import { Link } from "react-router-dom";

class ConfirmPage extends React.Component {

  render() {
    return (
      <div className="d-flex flex-row flex-wrap p-5 justify-content-center">
        <div className="container-fluid text-center p-5 border rounded border-dark">
          <h1 className="display-6">End of Questionnaire</h1>
          <hr />
          <p>You have completed the questionnaire!</p>
          <p>
            We ask that you please ensure that all your inputs are how
            you want them to be. If you ever want to change anything, you can
            update the questionnaire by going to your Profile and pressing on the
            'Update Questionnaire' button.
          </p>
          {/* Exit Button */}
          <Link to="/dashboard">
            <button
              type="button"
              className="btn btn-outline-primary me-3 mb-3"
              style={{ fontSize: 25 }}
            >
              Exit Questionnaire
            </button>
          </Link>
        </div>        
      </div>
    );
  }
}

export default ConfirmPage;
