import React from "react";
import "./articles.css";
import UserNav from "../../components/user-globals/user-navbar";
import defaultImage from "../../images/article-image.jpg";
import { Link } from "react-router-dom";
import axiosInstance from "../../axiosApi";

/**
 * Component that contains elements and functions related to listing out articles for users to see.
 *
 * Renders out a page meant to be routed through a navbar for user side.
 * 
 * TODO: Searchbar functionality
 *
 * @version 1.0.0
 */
class ArticlesPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      articles: [],
      activeItem: {
        id: null,
      },
    };
  }

  componentDidMount() {
    this.getArticles();
  }

  async getArticles() {
    await axiosInstance
      .get("/blog/post-list/")
      .then((result) => {
        this.setState({
          articles: result.data,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  render() {
    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <UserNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column m-5 border rounded bg-light">
          <h1 className="display-4 text-center">Articles</h1>
          <div className="input-group input-group-lg w-50 d-flex align-self-center">
            <span className="input-group-text" id="inputGroup-sizing-lg">
              Filter
            </span>
            <input
              type="text"
              className="form-control"
              aria-label="Sizing example input"
              aria-describedby="inputGroup-sizing-lg"
            ></input>
          </div>
          <hr />
          <div className="d-flex flex-wrap p-5 justify-content-around">
            {this.state.articles
              .filter((article) => article.isFeatured)
              .map((article, index) => {
                return (
                  <div key={index} className={article.id}>
                    <div className="card mb-3 border-primary" id="article-card">
                      <b className="p-2 text-center">Featured Article</b>
                      <img
                        src={defaultImage}
                        className="card-img-top"
                        alt=""
                      ></img>
                      <div className="card-body">
                        <h5 className="card-title">{article.title}</h5>
                        <p className="card-text">{article.description}</p>
                        <div className="d-flex flex-wrap">
                          <Link
                            to={"/dashboard/articles/article/id/" + article.id}
                            type="button"
                            className="btn btn-primary"
                          >
                            Expand Article
                          </Link>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}

            {this.state.articles
              .filter((article) => !article.isFeatured)
              .map((article, index) => {
                return (
                  <div key={index} className={article.id}>
                    <div className="card mb-3" id="article-card">
                      <img
                        src={defaultImage}
                        className="card-img-top"
                        alt=""
                      ></img>
                      <div className="card-body">
                        <h5 className="card-title">{article.title}</h5>
                        <p className="card-text">{article.description}</p>
                        <a>
                          <Link
                            to={{
                              pathname:
                                "/dashboard/articles/article/id/" + article.id,
                              state: article,
                            }}
                            type="button"
                            className="btn btn-primary"
                          >
                            Expand Article
                          </Link>
                        </a>
                      </div>
                    </div>
                  </div>
                );
              })}
          </div>
        </div>
      </div>
    );
  }
}

export default ArticlesPage;
