import React from "react";
import "../../components/admin-globals/admin-navbar";
import AdminNav from "../../components/admin-globals/admin-navbar";
import axiosInstance from "../../axiosAPi";

import BootstrapTable from "react-bootstrap-table-next";
import filterFactory, { textFilter } from "react-bootstrap-table2-filter";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import { Button } from "react-bootstrap";
import "./AdminViewUsers.css";
import AdminEdit from "./AdminEdit";


const { SearchBar } = Search;


let nameFilter;
let priceFilter;
let stockFilter;
let originFilter;

const ClearButton = props => {
  const handleClick = () => {
    props.onSearch("");
    props.clearAllFilter();
  };
  return (
    <Button
      variant="secondary"
      onClick={handleClick}
      style={{
        fontSize: "16px",
        padding: "5px",
        margin: "10px",
        height: "40px"
      }}
    >
      Clear
    </Button>
  );
};




class AdminViewFilterUsers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      users: [],
      activeItem: {
        id: null,
      },
      user_email: "",
      button_clicked: false,
    };

    this.fetchTasks = this.fetchTasks.bind(this);
    this.getCookie = this.getCookie.bind(this);
    
  }
  
  
 
  columns = [
    {
      dataField: "company",
      text: "Product Name",
      filter: textFilter({
        getFilter: filter => {
          nameFilter = filter;
        }
      }),
       sort: true
    },
    {
      dataField: "email",
      text: "Username/Email",
      filter: textFilter({
        getFilter: filter => {
          priceFilter = filter;
        }
      }),
      sort: true
    },
    {
      dataField: "first_name",
      text: "First Name",
      filter: textFilter({
        getFilter: filter => {
          stockFilter = filter;
        }
      }),
       sort: true
    },
    {
      dataField: "last_name",
      text: "Last Name",
      filter: textFilter({
        getFilter: filter => {
          originFilter = filter;
        }
      
      }),
       sort: true
    },
    
    {
      dataField: "View/Edit",
      text: "Follow",
      formatter: this.linkFollow,
    
    },


  ];

  
  clearAllFilter() {
    nameFilter("");
    priceFilter("");
    originFilter("");
    stockFilter("");
  }

  getCookie(name) {
    var cookieValue = null;
    if (document.cookie && document.cookie !== "") {
      var cookies = document.cookie.split(";");
      for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i].trim();
        // Does this cookie string begin with the name we want?
        if (cookie.substring(0, name.length + 1) === name + "=") {
          cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
          break;
        }
      }
    }
    return cookieValue;
  }

  componentDidMount() {
    this.fetchTasks();
  }

  async fetchTasks() {
    await axiosInstance
      .get("users/email-list/")
      .then((result) => {
        this.setState({
          users: result.data,
        });
      })
      .catch((error) => {
        throw error;
      });
  }

  
  handle_submit(e) {
    e.preventDefault();
    //Instead of doing another API call, it's better to have 2 array in this.state.
    //1 for storing all of the blogs and 1 for filtered data.
    //Show the filtered data to the user but use the all_blogs array to filter.
    //
    // e.preventDefault();
    // console.log("ITEM:", this.state.activeItem);
    // var csrftoken = this.getCookie("csrftoken");
    // var url = "http://localhost:8000/api/blog/post-list/";
    // fetch(url, {
    //   method: "GET",
    //   headers: {
    //     Authorization: "Basic " + btoa("401team:finovowelltothemoon"),
    //     "Content-type": "application/json",
    //     "X-CSRFToken": csrftoken,
    //   },
    // })
    //   .then((response) => {
    //     this.setState({
    //       activeItem: {
    //         id: null,
    //       },
    //     });
    //   })
    //   .catch(function (error) {
    //     console.log("ERROR: ", error);
    //   });
  }


  render() {

    return (
      <div className="d-flex flex-row overflow-hidden min-vh-100">
        <div className="col-2">
          {/* Side navigation bar*/}
          <AdminNav handle_logout={this.props.handle_logout} />
        </div>
        <div className="col d-flex flex-column flex-wrap m-5 p-5 bg-light">
          <h1 className="text-center">View / Edit User Information</h1>
          <hr />
          {/* Content */}

         <div className="container-fluid">
        
        <ToolkitProvider
          bootstrap4
          keyField="email"
          data={this.state.users}
          columns={this.columns}
          
          search
        >
          {props => (
            <div>
              <SearchBar
                {...props.searchProps}
                style={{ width: "600px", height: "40px" }}
                srText={["Search the user by putting in any of the following table headings"]}
              />
              <ClearButton
                {...props.searchProps}
                clearAllFilter={this.clearAllFilter}
              />
              <BootstrapTable
                {...props.baseProps}
                filter={filterFactory()}
                keyField="email"
                noDataIndication="There is no solution"
                striped
                hover
                condensed

                
              />
              
            </div>
          )}
        </ToolkitProvider>
      </div>
      </div>
</div>
      
    );
    
  }
  
}

export default AdminViewFilterUsers;